body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0f380f;
  background-image: 
    linear-gradient(45deg, #0f380f 25%, transparent 25%),
    linear-gradient(-45deg, #0f380f 25%, transparent 25%),
    linear-gradient(45deg, transparent 75%, #0f380f 75%),
    linear-gradient(-45deg, transparent 75%, #0f380f 75%);
  background-size: 20px 20px;
  background-position: 0 0, 0 10px, 10px -10px, -10px 0px;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

#root {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
}

.game-container {
  background-color: #9bbc0f;
  border: 8px solid #306230;
  border-radius: 8px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  overflow: hidden;
  position: relative;
}

.game-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, rgba(155, 188, 15, 0.1) 25%, transparent 25%),
              linear-gradient(-45deg, rgba(155, 188, 15, 0.1) 25%, transparent 25%),
              linear-gradient(45deg, transparent 75%, rgba(155, 188, 15, 0.1) 75%),
              linear-gradient(-45deg, transparent 75%, rgba(155, 188, 15, 0.1) 75%);
  background-size: 20px 20px;
  background-position: 0 0, 0 10px, 10px -10px, -10px 0px;
  pointer-events: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
} 